import {  useState } from 'react';
import { LineChart, XAxis, YAxis, CartesianGrid, Tooltip, Legend, Line,ResponsiveContainer  } from 'recharts';
import 'antd/dist/antd.css';
import { Button, DatePicker ,Row, Col , Card  ,Space,message,  } from 'antd';



function CovidMainBloc(params: any) {
  const { RangePicker } = DatePicker;
  const [dataNumber, setDataNaber] = useState([])// получение дат из датапикера 
  const [dataChart, setDataChart] = useState([]);// передача статистики в граффик
  const [dataAll, setDataAll] = useState([])
  const [dataAllDeaths, setAllDeaths] = useState([])
  const [dataAllRecovered, setAllRecovered] = useState([])

  function onChange(date: any, dateString: any) { // получение значения датапикера 
    setDataNaber(dateString)
  } 

  

  async function inputValue() { // обработка инпута с данными
    console.log(dataNumber[0]) // проверка приходят ли данные 
    console.log(dataNumber[1])

    const todayNewConfirmed: any = [] // Cтатистика по заболевшим 
    const death: any = [] // Cтатистика по умершим
    const recover: any = [] // Cтатистика по выздоровевшим
    const dni: any = [] // массив  дат для отрисовки 

    await fetch(`https://api.covid19tracking.narrativa.com/api/country/russia?&date_from=${dataNumber[0]}&date_to=${dataNumber[1]}`)
      .then(res => res.json())
      .then(datas => {
        console.log(datas)
        const labels = Object.keys(datas.dates);

        // перебор данных 
        
        setDataAll(datas.total.today_new_confirmed)
        setAllDeaths(datas.total.today_new_deaths)
        setAllRecovered(datas.total.today_new_recovered)

        for (let label of labels) {
          todayNewConfirmed.push(datas.dates[label].countries['Russia'].today_new_confirmed);
        }
        for (let label of labels) {
          death.push(datas.dates[label].countries['Russia'].today_new_deaths);
        }
        for (let label of labels) {
          recover.push(datas.dates[label].countries['Russia'].today_new_recovered)
        }
        for (let label of labels) {
          dni.push(datas.dates[label].countries['Russia'].date)
        }

      })
      
      
    const data: any = [] // массив для хука графика

    for (let i = 0; i < dni.length; i++) { // заполнения формы работы графика 
      data.push({
        "name": dni[i],
        "Cтатистика по заболевшим": todayNewConfirmed[i],
        'Cтатистика по умершим': death[i],
        'Cтатистика по выздоровевшим': recover[i],
      })
    }
    
    setDataChart(data) // хук графика 
    console.log(dataAll) // провека хука 
    

    if(data !==''){
      message.success('Данные получены');
    } else{
      message.error('Ошибка!');
    }
  }
  return (
    <>
    <Row>
        <Col span={12} offset={8}>
        <Space direction="vertical" size={12}>
          <RangePicker onChange={onChange} /> <Button type="primary"  onClick={inputValue}>Получить статистику</Button>
        </Space>
        </Col>
      </Row>
      <Row>
        <Col span={5} offset={8}>
          <Card title='Статистика по всему миру '  bordered={false} style={{width:400 , marginTop:50 }}>
            <h1>За периуд {dataNumber[0]} - {dataNumber[1]}</h1>
            <p>Cтатистика по заболевшим: {dataAll}</p>
            <p>Cтатистика по умершим: {dataAllDeaths}</p>
            <p>Cтатистика по выздоровевшим: {dataAllRecovered}</p>
          </Card>
        </Col>
      </Row>
    <Row>
     <Col span={12} offset={4} style={{top:20}}>
       <ResponsiveContainer width="100%" height={400}>
        <LineChart width={500} height={300} data={dataChart} margin={{ top: 100, right: 5, left: 5, bottom: 5 }} style={{backgroundColor:'#ffffff'}}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Line type="monotone" dataKey="Cтатистика по заболевшим" stroke="#8884d8" />
          <Line type="monotone" dataKey="Cтатистика по умершим" stroke="#fe0015" />
          <Line type="monotone" dataKey="Cтатистика по выздоровевшим" stroke="#82ca9d" />
        </LineChart>
      </ResponsiveContainer>  
     </Col>
    </Row> 
   </>
  )
}
export default CovidMainBloc;